/* =-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-
    FONTS
=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=*/
// font families
@import url(https://fonts.googleapis.com/css?family=Sancreek);
@import url(https://fonts.googleapis.com/css?family=Sancreek|Slabo+27px);

$sancreek: 'Sancreek', cursive;
$slabo: 'Slabo 27px', serif;

.sancreek {
    font-family: $sancreek;
}

.slabo {
    font-family: $slabo;
}

.center_text {
    text-align: center;
}

/* =-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-
    COLOR SCHEME
=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=*/

$brown_green: #C5AD4B;
$dark_brown: #3F2C26;
$coral_red: #DD423E;

/* =-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-
    UNIVERSAL
=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=*/

html, body {
    height: 100%;
    min-height: 100%;
    display: block;
    font-family: $sancreek;
    background: url('../images/cheap_diagonal_fabric.png');
    background-attachment: fixed;
    overflow-x: hidden;
}

h1, h2, h3, h4, h5, h6 {
    font-family: $sancreek;
    font-weight: 700;
}

h1 {
    font-size: 10vw;
    margin: 2% 0;
}

h2 {
    font-size: 8vw;
    margin: 2% 0;
}

h3 {
    font-size: 6vw;
    margin: 2% 0;
}

h4 {
    font-size: 4vw;
    margin: 2% 0;
}

a {
    color: $coral_red;
    transition: all .2s ease-out;
    text-decoration: none;
}

a:hover {
    color: $dark_brown;
}

$paper_background: url('../images/linedpaper.png');

.full_width {
    max-width: 100%;
    height: auto;
    width: 100%;
}

.page_wrap {
    max-width: 90%;
    width: 1200px;
    margin: 0 auto;
    display: block;
}

::selection {
    background: $coral_red;
    color: $dark_brown;
}

/* =-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-
    GITHUB BANNER
=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=*/

.githubBanner {
    background: $paper_background;
    position: absolute;
    top: 34px;
    right: -120px;
    width: 400px;
    transform: rotate(40deg);
    box-shadow: 0 0 12px #000;
    a {
        display: inline-block;
        margin: 0 auto;
        width: 400px;
    }
    a h2 {
        font-size: 16px;
    }
}

/* =-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-
    INTRO
=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=*/

.intro {
    .fa-github-alt {
        display: block;
        font-size: 4vw;
    }
}

.instructions, .info {
    display: inline-block;
    vertical-align: top;
    width: 49%;
}
